bizagi.workportal.services.service.extend("bizagi.workportal.services.service", {}, {        

    getTimeLineActivities: function (radNumber) {
        var self = this;
        var url = self.serviceLocator.getUrl("project-timeline-events-get");
        return $.read({
            url: encodeURI(url.replace("{idCaseParamValue}", radNumber)),
            dataType: "json",
            contentType: "application/json"
        });
    },

    getAssigneePicture: function (usersGuids) {
        var self = this;
        var params = {
            userIds: usersGuids.join(),
            width: 50,
            height: 50
        };

        return self.getUsersData(params);
    },

    getItemsAssignees: function (idWorkitem) {
        var self = this;
        var url = self.serviceLocator.getUrl("project-workitem-assignees");
        return $.read({
            url: url.replace("{idWorkitem}", idWorkitem),
            dataType: "json",
            contentType: "application/json"
        });
    },

    getItemsAssignees: function (idWorkitem) {
        var self = this;
        var url = self.serviceLocator.getUrl("project-workitem-assignees-items");
        return $.read({
            url: url.replace("{idWorkitem}", idWorkitem),
            dataType: "json",
            contentType: "application/json"
        });
    }
    
});
