$.Class.extend("bizagi.workportal.services.windowMaintenance", {}, {

    init: function (dataService) {
        var self = this;
        self.content = $('#ui-bizagi-wp-application');
        self.showLessButton = false;
        self.readLessHtml = ' <span id="read_less">'+bizagi.localization.getResource('workportal-general-word-read-less')+'</span>';
        self.readMoreHtml = '... <span id="read_more">'+ bizagi.localization.getResource('workportal-general-word-read-more')+'</span>';
        
        bizagi.showErrorAlertDialog = false;
        if(dataService !== null && dataService !== undefined){
            $.when(dataService.getMaintenanceWindowShowUpcoming()).done(function (response) {
                if(response !== null){
                    self.maintenanceMessage = response;
                    self.showMaintenanceMessage();
                }
            }).fail(function(){
            }).always(function() {
                setTimeout(function(){
                    bizagi.showErrorAlertDialog = true;
                }, 1);
            });
        }
    },
    showMaintenanceMessage: function(){
        var self = this;
        var elementMaintenanceText = $('.mantainance__text', self.content);

        if(self.maintenanceMessage && !bizagi.util.isEmpty(self.maintenanceMessage)){
            var activateWindow = false;
            
            if((bizagi.services.preferences().getPreference("maintenanceGuid") != self.maintenanceMessage.id) || 
            (bizagi.services.preferences().getPreference("maintenanceModifiedDate") != self.maintenanceMessage.modifiedDate)){
                activateWindow = true;
            }
            
            if(activateWindow){
               
                elementMaintenanceText.append(self.maintenanceMessage.message);
                $('.mantainance__conatainer', self.content).show();

                self.closeMaintenanceMessage();
                self.showLater();
                self.initialContent = elementMaintenanceText[0].textContent;
                self.ellipsizeTextBox(elementMaintenanceText);
                self.resizeWindow(elementMaintenanceText);
            }
        }   
    },
    /** Add ellipsis and read more button if it is necessary **/
    ellipsizeTextBox: function (element) {
        var self = this;
        var el = element[0];
        var wordArray = el.textContent.trim().split(' ');
    
        while(el.scrollHeight > el.offsetHeight+10 && wordArray.length>0) {
            self.showLessButton = false;
            wordArray.pop();
            self.finalContent = wordArray.join(' ') + self.readMoreHtml;
            el.innerHTML = self.finalContent;
         }
         self.handlerReadMoreLink(element);
    },
    /** When users resize the browser window **/
    resizeWindow: function (elementMaintenanceText){ 
        var self = this;
        $(window).resize(function() {

            if(self.showLessButton === true){ /*If the less button is shown, it has to continue to show at least the content is adjusted to two lines (42px + tolerance)*/

                if(elementMaintenanceText[0].offsetHeight <= 52){
                    elementMaintenanceText[0].innerHTML = self.initialContent;
                }else{
                    elementMaintenanceText[0].innerHTML = self.initialContent + self.readLessHtml; /** Initialize content **/ 
                }
               
                self.handlerReadLessLink(elementMaintenanceText, true);
            }else{ /*If there is no a less button is shown, the content has to be initialize to recalculate the size of the content to be shown*/

                elementMaintenanceText[0].innerHTML = self.initialContent; /** Initialize content **/ 

            }
            self.ellipsizeTextBox(elementMaintenanceText);
        });
    },
    readMore: function(element){

        var self = this;
        element.removeClass('height-read-more');
        element[0].innerHTML = self.initialContent + self.readLessHtml;
        self.handlerReadLessLink(element);

    },

    readless: function(element, resize){

        var self = this;
        element.addClass('height-read-more');
        if(resize === true){
            self.ellipsizeTextBox(element); /*Adjust the content again*/
        }else{
            element[0].innerHTML = self.finalContent;
        }
        self.handlerReadMoreLink(element);

    },

    handlerReadMoreLink: function(element){

        var self = this;
        $('#read_more').click(function(){
            self.showLessButton = true;
            self.readMore(element);
        });

    },

    handlerReadLessLink: function(element, resize){

        var self = this;
        $('#read_less').click(function(){
            self.readless(element, resize);
        }); 

    },

    closeMaintenanceMessage: function(saveGuid){

        var self = this;
        saveGuid = saveGuid?saveGuid:true;
        //var maintenanceMessage = bizagiConfig.maintenanceAlert;
        var guid = self.maintenanceMessage.id;
        var modifiedDate = self.maintenanceMessage.modifiedDate;
        $('.mantainance__close', self.content).click(function(){
            $('.mantainance__conatainer', self.content).hide();
            if(saveGuid){
                self.setMaintenacePreferences(guid, modifiedDate);
            }

        });
    },
    showLater: function(){
        var self = this;
        $('.maintenance_show_later__button', self.content).click(function(){
            $('.mantainance__conatainer', self.content).hide();
        });  
    },
    setMaintenacePreferences: function(guid, modifiedDate) {
        bizagi.services.preferences().addPreference("maintenanceGuid", guid)
        bizagi.services.preferences().addPreference("maintenanceModifiedDate", modifiedDate)                   
    }

});